.course-standards-container {
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1100px;
}

.course-standards-title {
    font-size: 40px;
    font-weight: bold;
}

.course-standards-subtitle {
    font-size: 14px;
    color: #888888;
    margin-bottom: 70px;
}

.course-standards-error {
    padding: 16px;
    background-color: #FEF2F2;
    color: #EF4444;
    border-radius: 8px;
    text-align: center;
    margin-top: 20px;
    border: 1px solid #FECACA;
}

@media (max-width: 768px) {
    .course-standards-container {
        padding: 50px 10px 0;
    }

    .course-standards-title {
        font-size: 24px;
        margin-top: 10px;
        margin-left: 15px;
    }

    .course-standards-subtitle {
        margin-bottom: 20px;
        margin-left: 15px;
    }
}